//import logo from './logo.svg';
//import './App.css';

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/experience" component={WorkExperiencePage} />
          <Route path="/" component={HomePage} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
